import { AgendamentoPage } from './../agendamento/agendamento';
import { HomePage } from './../home/home';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ModalController, ToastController } from 'ionic-angular';
import { AgendaProvider } from './../../providers/agenda/agenda';
import swal from 'sweetalert';

@IonicPage()
@Component({
  selector: 'page-agendados',
  templateUrl: 'agendados.html',
})
export class AgendadosPage {

  servicos: any = null;

  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    private serviceAgenda: AgendaProvider,
    private modalCtrl: ModalController,
    private toastCtrl: ToastController,
    private loadingCtrl: LoadingController) {
  }

  carregarServicos() {
    const loader = this.loadingCtrl.create({
      content: "Carregando...",
      spinner: 'bubbles'
    });

    loader.present();

    this.serviceAgenda.buscar(false).then(resp => {
      loader.dismiss();

      if (resp.json().length > 0) {
        this.servicos = resp.json();
      } else {
        swal({
          title: "Atenção!",
          text: "Não tem serviços de agendamento em aberto!",
          timer: 2000,
          icon: "info"
        });

        this.navCtrl.setRoot(HomePage);
      }
    })
  }

  ionViewWillEnter() {
    this.carregarServicos();
  }

  mensagemExpirado() {
    this.toastCtrl.create({
      message: 'Limite para remarcar excedido',
      duration: 2000
    }).present();
  }

  remarcar(servico) {
    // let dia = new Date().getUTCDate();
    // let mes = new Date().getMonth() + 1;

    // let diaMarcado = parseInt(servico.data_agendamento.substring(0, 2));
    // let mesMarcado = parseInt(servico.data_agendamento.substring(5, 3));
    // let anoMarcado = parseInt(servico.data_agendamento.substring(10, 6));

    // let diaLimite = dia - 2;
    // let mesLimite = mes;
    // let anoLimite = new Date().getFullYear();

    // if (dia <= 2) {
    //   mesLimite -= 1;

    //   if (mesLimite == 0) {
    //     mesLimite = 12;
    //     anoLimite -= 1;
    //   }

    //   diaLimite = new Date(anoLimite, mesLimite, 0).getDate();

    //   if (dia == 1) {
    //     diaLimite -= 1;
    //   }
    // }

    // if (mesMarcado == mesLimite) {
    //   if (diaMarcado > diaLimite) {
    //     return this.mensagemExpirado();
    //   }
    // } else if (mesMarcado < mesLimite) {
    //   return this.mensagemExpirado();
    // } else if (mesMarcado < mesLimite) {
      
    // }


  //  console.log(diaLimite);
    // console.log(mesMarcado)




    let modal = this.modalCtrl.create(AgendamentoPage, { item: servico });
    modal.present();

    modal.onDidDismiss(data => {
      if (data) {
        this.servicos[parseInt(data.index) - 1].data_agendamento = data.data_agendada;
        this.servicos[parseInt(data.index) - 1].hora_agendamento = data.hora_agendada;

        this.serviceAgenda.remarcar(this.servicos[parseInt(data.index) - 1]).then(() => {
          swal({
            title: "Salvo!",
            text: "Remarcado com sucesso!",
            timer: 2000,
            icon: "success"
          });

          this.carregarServicos();
        }).catch(resp => {
          console.log(resp);
        })
      }
    });
  }
}



